<template>
    <div class="home">
        <el-container style="height: 100vh;">
            <el-aside width="200px" style="background-color: #DCDFE6;overflowX:hidden">
                <el-menu class="el-menu-vertical-demo" router background-color="#DCDFE6" text-color="#333" active-text-color="#61a0a8" :default-active="this.$route.path">
                    <el-menu-item index="/admin">
                        <i class="el-icon-s-home"></i>
                        <span slot="title"></span>
                    </el-menu-item>
                    <el-submenu index="article">
                        <template slot="title">
                            <i class="el-icon-document"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="/article/new"></el-menu-item>
                            <el-menu-item index="/article/list"></el-menu-item>
                            <el-menu-item index="/themetype/new"></el-menu-item>
                            <el-menu-item index="/themetype/list"></el-menu-item>
                            <el-menu-item index="/comment/new"></el-menu-item>
                            <el-menu-item index="/comment/list"></el-menu-item>
                            <el-menu-item index="/commentreply/list"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="message">
                        <template slot="title">
                            <i class="el-icon-chat-dot-square"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="/message/new"></el-menu-item>
                            <el-menu-item index="/message/list"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="notice">
                        <template slot="title">
                            <i class="el-icon-s-order"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="/notice/new"></el-menu-item>
                            <el-menu-item index="/notice/list"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="links">
                        <template slot="title">
                            <i class="el-icon-link"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="/link/new"></el-menu-item>
                            <el-menu-item index="/link/list"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="information">
                        <template slot="title">
                            <i class="el-icon-edit-outline"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="/information/new"></el-menu-item>
                            <el-menu-item index="/admin/edit"></el-menu-item>
                            <!-- <el-menu-item index="/information/list"></el-menu-item> -->
                        </el-menu-item-group>
                    </el-submenu>
                    <!-- <el-submenu index="admin" v-if="is_super == 1 ? true : false">
                        <template slot="title">
                            <i class="el-icon-edit"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="/admin/edit"></el-menu-item>
                            <el-menu-item index="/admin/list"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu> -->
                </el-menu>
            </el-aside>

            <el-container>
                <el-header style="text-align: right; font-size: 12px;">
                    <el-dropdown @command="handleCommand">
                        <i class="el-icon-setting" style="margin-right: 15px"></i>
                        <el-dropdown-menu slot="dropdown">
                            <!-- <el-dropdown-item command='info'></el-dropdown-item> -->
                            <el-dropdown-item command='out'></el-dropdown-item>
                        </el-dropdown-menu>
                        <span>{{ username }}</span>
                    </el-dropdown>
                </el-header>
                <el-main style="background:#fafafa">
                    <router-view :key="$route.path"></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import api from "../api";
export default {
    data() {
        return {
            is_super: "",
            username: "",
        };
    },
    methods: {
        handleCommand(val) {
            switch (val) {
                case "info":
                    this.toInfo();
                    break;
                case "out":
                    this.loginOut();
                    break;
                default:
                    break;
            }
        },
        toInfo() {
            this.$router.push({
                path: "/admin/info",
            });
        },
        loginOut() {
            localStorage.removeItem("token");
            this.$router.go(0);
        },
        async fetch() {
            const res = await api.admin.getAdmin();
            this.is_super = res.data.data.is_super;
            this.username = localStorage.getItem("username");
        },
    },
    created() {
        this.fetch();
    },
};
</script>

<style>
.el-header {
    background-color: #dcdfe6;
    color: #333;
    line-height: 60px;
}
</style>
